import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/pages/pc/layout/index'

const routerPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(error => error)
}
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    { path: '/404', component: () => import('@/pages/404') },
    {
      path: '/',
      name: 'pages',
      component: () => import('@/pages/index')
    },
    {
      path: '/mobile',
      name: 'mobile',
      redirect: '/mobile/user',
      component: () => import('@/pages/mobile/index'),
      children: [
        // {
        //   // 当 /user/:id/profile 匹配成功，
        //   // UserProfile 会被渲染在 User 的 <router-view> 中
        //   path: 'home',
        //   name: 'mobile首页',
        //   component: () => import('@/pages/mobile/home/index')
        // },
        {
          path: 'user',
          name: 'user',
          component: () => import('@/pages/mobile/user/index')
        },
        {
          path: 'user/password',
          name: '用户密码',
          component: () => import('@/pages/mobile/user/password/index')
        },
        // {
        //   path: 'institutions',
        //   name: 'mobile_institutions',
        //   component: () => import('@/pages/mobile/institutions/index')
        // },
        // {
        //   path: 'institutions/password',
        //   name: 'mobile_InstitutionsPassWord',
        //   component: () => import('@/pages/mobile/institutions/password')
        // },
        {
          path: 'retrieve/phone-way',
          name: '手机号找回',
          component: () => import('@/pages/mobile/account/retrieve/phone-way')
        },
        {
          path: 'retrieve/email-way',
          name: '邮箱找回',
          component: () => import('@/pages/mobile/account/retrieve/email-way')
        },
        {
          path: 'retrieve/artificial-way',
          name: '人工找回',
          component: () => import('@/pages/mobile/account/retrieve/artificial-way')
        },
        {
          path: 'retrieve',
          name: 'm_retrieve',
          component: () => import('@/pages/mobile/account/retrieve/index')
        },
        {
          path: 'mbind/:uuid/:code',
          name: 'mbind',
          component: () => import('@/pages/mobile/account/bind/index')
        },
        {
          path: 'author',
          name: 'author',
          component: () => import('@/pages/mobile/author/index')
        },
        {
          path: 'pass/:phone(\\d{11})',
          name: 'pass',
          component: () => import('@/pages/mobile/account/retrieve/pass'),
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'account',
          name: 'm_account',
          component: () => import('@/pages/mobile/account/index'),
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'personal',
          name: 'm_personal',
          component: () => import('@/pages/mobile/account/personal'),
          meta: {
            requiresAuth: true
          }
        },
      ]
    },
    {
      path: '/pc',
      name: 'pc',
      redirect: '/pc/login',
      component: Layout,
      children: [
        {
          path: 'login',
          name: 'pc首页',
          component: () => import('@/pages/pc/login/index')
        },
        {
          path: 'agreement',
          name: 'agreement',
          component: () => import('@/pages/pc/agreement/index')
        },
        //机构
        // {
        //   path: 'institutions',
        //   name: 'institutions',
        //   component: () => import('@/pages/pc/institutions/index'),
        // },
        {
          path: 'retrieve',
          name: 'retrieve',
          component: () => import('@/pages/pc/retrieve/index')
        },
        {
          path: 'account',
          name: 'account',
          component: () => import('@/pages/pc/account/index'),
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'personal',
          name: 'personal',
          component: () => import('@/pages/pc/account/personal'),
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'bind/:uuid/:code',
          name: 'bind',
          component: () => import('@/pages/pc/account/bind/index')
        },
        {
          path: 'email',
          name: 'email',
          component: () => import('@/pages/pc/account/email')
        },
        {
          path: 'password/:phone(\\d{11})',
          name: 'password',
          component: () => import('@/pages/pc/account/password'),
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'update_pwd',
          name: 'update_pwd',
          component: () => import('@/pages/pc/account/update_pwd')
        },
        {
          path: 'update_phone',
          name: 'update_phone',
          component: () => import('@/pages/pc/account/update_phone')
        },
      ]
    },
    { path: '*', redirect: '/404', hidden: true }
  ]
})



