import Cookies from 'js-cookie'

const TokenKey = 'Token'
const RefreshKey = 'Refresh-Token'
const RedirectObject = 'redirect-object'
const IP = 'IP'

export function getToken() {
    return Cookies.get(TokenKey)
}

export function setToken(token) {
    // var inFifteenMinutes = new Date(new Date().getTime() + 2 * 60 * 60 * 1000);
    var inFifteenMinutes = new Date(new Date().getTime() + 2 * 60 * 60 * 1000);
    return Cookies.set(TokenKey, token, { expires: inFifteenMinutes })
}

export function removeToken() {
    return Cookies.remove(TokenKey)
}
export function getRefreshToken() {
    return Cookies.get(RefreshKey)
}

export function setRefreshToken(token) {
    return Cookies.set(RefreshKey, token, { expires: 7 })
}

export function removeRefreshToken() {
    return Cookies.remove(RefreshKey)
}
export function getUrl() {
    return JSON.parse(sessionStorage.getItem(RedirectObject))
}

export function setUrl(object) {
    object = JSON.stringify(object);
    return sessionStorage.setItem(RedirectObject, object);
}

export function removeUrl() {
    return sessionStorage.removeItem(RedirectObject)
}

export function getIp() {
    return Cookies.get(IP)
}

export function setIp() {
    getUserIP(ip => {
        Cookies.set(IP, ip)
    })

}

export const getUserIP = onNewIP => {
    let MyPeerConnection =
        window.RTCPeerConnection ||
        window.mozRTCPeerConnection ||
        window.webkitRTCPeerConnection;
    let pc = new MyPeerConnection({
        iceServers: []
    });
    let noop = () => { };
    let localIPs = {};
    let ipRegex = /([0-9]{1,3}(\.[0-9]{1,3}){3}|[a-f0-9]{1,4}(:[a-f0-9]{1,4}){7})/g;
    let iterateIP = ip => {
        if (!localIPs[ip]) onNewIP(ip);
        localIPs[ip] = true;
    };
    pc.createDataChannel("");
    pc.createOffer()
        .then(sdp => {
            sdp.sdp.split("\n").forEach(function (line) {
                if (line.indexOf("candidate") < 0) return;
                line.match(ipRegex).forEach(iterateIP);
            });
            pc.setLocalDescription(sdp, noop, noop);
        })
        .catch(reason => { });
    pc.onicecandidate = ice => {
        if (
            !ice ||
            !ice.candidate ||
            !ice.candidate.candidate ||
            !ice.candidate.candidate.match(ipRegex)
        )
            return;
        ice.candidate.candidate.match(ipRegex).forEach(iterateIP);
    };
}



