import { login_from } from "@/api/login";
import { agent_token_redirect, getToken_refresh } from "@/api/user";
import { login_password_from } from "@/api/login";
import { getToken, setToken, removeToken, getRefreshToken, setRefreshToken, removeRefreshToken, getUrl } from '@/utils/auth'
import { qrStatus } from "@/api/code";
import { wechat_auth_api, setPhone, removeTokens } from "@/api/user"

const user = {
    state: {
        token: getToken(),
        refresh_token: getRefreshToken()
    },

    mutations: {
        SET_TOKEN: (state, token) => {
            state.token = token
        },
        SET_REFRESH_TOKEN: (state, refresh_token) => {
            state.refresh_token = refresh_token
        },
    },

    actions: {
        //刷新token
        refresh_token({ dispatch, commit, state }) {
            const token = getToken()
            return new Promise((resolve, reject) => {
                if (token) {
                    dispatch('getAgent_token', { root: true }).catch(error => {
                        reject(error)
                    })
                } else {
                    if (state.refresh_token) {
                        getToken_refresh(state.refresh_token).then(res => {
                            const data = res.data
                            setToken(data.token)
                            commit('SET_TOKEN', data.token)
                            dispatch('getAgent_token', { root: true }).catch(error => {
                                reject(error)
                            })
                        }).catch(err => {
                            removeRefreshToken()
                            // router.push('/')
                        })
                    } else {
                        resolve('refresh_token过期需登录')
                        // router.push('/')
                    }
                }
            })
        },
        //微信授权
        wechat_auth({ commit, dispatch }, Info) {
            // console.log(Info)
            const { code, appid } = Info
            return new Promise((resolve, reject) => {
                wechat_auth_api({ code, appid }, appid).then(res => {
                    const data = res.data;
                    if (data.bind_status) {
                        setToken(data.token.token)
                        setRefreshToken(data.token.refresh_token)
                        commit('SET_TOKEN', data.token.token)
                        commit('SET_REFRESH_TOKEN', data.token.refresh_token)
                        dispatch('getAgent_token', { root: true }).catch(error => {
                            reject(error)
                        })
                    } else {
                        resolve(data)
                    }
                })
            })
        },
        //微信登录
        wechat_login({ commit, dispatch }, Info) {
            const { appid, scene_str } = Info
            return new Promise((resolve, reject) => {
                qrStatus({ appid, scene_str }).then(res => {
                    // res = res.parseJSON()
                    const data = res.data;
                    if (data.qrcode_login_status) {
                        // const user = res.data.wechat
                        const token = res.data.token
                        if (data.bind_status && token) {
                            setToken(token.token)
                            setRefreshToken(token.refresh_token)
                            commit('SET_TOKEN', token.token)
                            commit('SET_REFRESH_TOKEN', token.refresh_token)
                            dispatch('getAgent_token', { root: true }).catch(error => {
                                reject(error)
                            })
                        } else {
                            resolve(data)
                        }
                    }
                })
            })
        },
        //绑定手机号
        bind_phone({ dispatch, commit }, Info) {
            const data = Info
            // console.log(data)
            return new Promise((resolve, reject) => {
                setPhone(JSON.stringify(data)).then(response => {
                    const token = response.data.token
                    if (token) {
                        setToken(token.token)
                        setRefreshToken(token.refresh_token)
                        commit('SET_TOKEN', token.token)
                        commit('SET_REFRESH_TOKEN', token.refresh_token)
                        dispatch('getAgent_token', { root: true }).then(() => {
                            resolve(1)
                        }).catch(error => {
                            reject(error)
                        })
                    } else {
                        resolve(0)
                    }
                })
            })
        },
        // 短信登录
        Login({ dispatch, commit }, userInfo) {
            return new Promise((resolve, reject) => {
                // console.log(userInfo)
                const { phone, code } = userInfo
                let obj = JSON.stringify({ phone_num: phone, code });
                login_from(obj).then(response => {
                    const fromdata = response.data.token
                    setToken(fromdata.token)
                    setRefreshToken(fromdata.refresh_token)
                    commit('SET_TOKEN', fromdata.token)
                    commit('SET_REFRESH_TOKEN', fromdata.refresh_token)
                    if (!response.data.note.pass_set_status) {
                        resolve(0)
                    } else {
                        dispatch('getAgent_token', { root: true }).catch(error => {
                            reject(error)
                        })
                    }
                })
            })
        },
        //获取业务token
        getAgent_token({ state, dispatch }) {
            const RedirectObj = getUrl()
            // const device_type = Cookies.get('device_type');
            if (state.token) {
                return new Promise((resolve, reject) => {
                    if (RedirectObj) {
                        try {
                            agent_token_redirect({
                                token: state.token,
                                agent_id: RedirectObj.agentID,
                                redirect_url: RedirectObj.redirect_url.match(/(([\w-]+\.)+\w+(:\d{1,5})?)/)[1]
                                // agent_id: '12e907b4-fb06-4880-8fec-00083c096b17',
                                // redirect_url: "https://nixingzhe.qisuxinxi.com/#/".match(/(([\w-]+\.)+\w+(:\d{1,5})?)/)[1]
                            }).then(res => {
                                // console.log(res)
                                const data = res.data
                                // console.log(data.agent_token)
                                if (data.agent_token) dispatch('redirect', { agent_token: data.agent_token }, { root: true })
                                resolve('redirect')
                            }).catch(error => {
                                reject(error)
                            })
                        } catch (error) {
                            reject('agent_token:' + error)
                        }
                    } else {
                        reject('redirect_object:undefined')
                    }
                })
            } else {
                dispatch('refresh_token', { root: true })
            }
        },
        //密码登录
        login_password({ dispatch, commit }, userInfo) {
            // console.log(userInfo)
            const { userName, password } = userInfo
            let obj = JSON.stringify({ user_name: userName, password });
            return new Promise((resolve, reject) => {
                login_password_from(obj).then(res => {
                    const data = res.data.token
                    setToken(data.token)
                    setRefreshToken(data.refresh_token)
                    commit('SET_TOKEN', data.token)
                    commit('SET_REFRESH_TOKEN', data.refresh_token)
                    dispatch('getAgent_token').then((res) => {
                        resolve(res)
                    }).catch(error => {
                        reject(error)
                    })
                })
            })
        },
        //登出
        LogOut({ commit }) {
            return new Promise((resolve, reject) => {
                removeTokens().then(() => {
                    commit('SET_TOKEN', '')
                    commit('SET_ROLES', [])
                    removeToken()
                    resolve()
                }).catch(error => {
                    reject(error)
                })
            })
        },
    }
}

export default user



