import App from "@/utils/cofigApp";
export default {
    install(Vue, options) {
        Vue.prototype.$wechat_href = function () { 
            wxLogin()
        };
        Vue.prototype.$reCaptcha = function (event, actionName) {
            event.preventDefault();
            return new Promise((resolve, reject) => {
                grecaptcha.ready(function () {
                    grecaptcha.execute('6Ld1OqYUAAAAAPdpFHtZwMe-haSLDuaMI9fPxYsh', { action: actionName }).then(res => {
                        // console.log(res)
                        // console.log('reCaptcha')
                        resolve(res)
                    })
                })
            })
        }
    }
}
const wxLogin = () => {
    // console.log(app)
    let appid = encodeURIComponent(App.appid)
    let redirect_uri = encodeURIComponent(App.url)
    window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appid}&redirect_uri=${redirect_uri}&response_type=code&scope=snsapi_userinfo&state=fanke#wechat_redirect`
    //https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx944397cf9748037c&redirect_uri=http://v8gquv.natappfree.cc/&response_type=code&scope=snsapi_userinfo&state=fanke#wechat_redirect
}



