import axios from 'axios'
import { Message } from 'element-ui'
import { Toast } from 'vant';
import store from '@/store'
import { getToken, getIp, getRefreshToken, removeToken, removeRefreshToken } from '@/utils/auth'
import router from '@/router'

// create an axios instance
const service = axios.create({
  // baseURL: '/api',
  baseURL: process.env.BASE_API, // api 的 base_url
  timeout: 8000 // request timeout
})

// request interceptor
service.interceptors.request.use(
  config => {
    // Do something before request is sent
    if (getToken()) {
      // 让每个请求携带token-- ['X-Token']为自定义key 请根据实际情况自行修改
      config.headers['token'] = getToken()
    }
    if (getRefreshToken()) {
      config.headers['refresh_token'] = getRefreshToken()
    }
    if (store.getters.ip) {
      config.headers['X-REAL-IP'] = getIp()
    }
    return config
  },
  error => {
    // Do something with request error
    console.log(error) // for debug
    Promise.reject(error)
  }
)

// response interceptor
service.interceptors.response.use(
  response => {
    const res = response.data
    if (res.status_code !== 1) {
      if (store.getters.device_type == "pc") {
        Message({
          message: res.errmsg,
          type: 'error',
          duration: 5 * 1000
        })
      }
      if (store.getters.device_type == "mobile") {
        Toast(res.errmsg)
      }
      // if (res.status_code === 40163 || res.status_code === 10027) {
      //   router.push('/')
      // }
      // //refresh_token无效或者不存在
      // if (res.status_code === 22003 || res.status_code === 22006 || res.status_code === 10105) {
      //   removeToken();
      //   removeRefreshToken()
      //   router.push('/')
      // }
      //refresh_token无效
      if (res.status_code === 10107) {
        removeToken();
        removeRefreshToken()
        router.push('/')
      }
      //token无效或者不存在
      if (res.status_code === 10101 || res.status_code === 10102 || res.status_code === 10103 || res.status_code === 10104 || res.status_code === 10105 || res.status_code === 10106 || res.status_code === 10108 || res.status_code === 10109) {
        removeToken();
        router.go(0);
        store.dispatch('refresh_token')
      }
      //授权code无效或参数undefined
      if (res.status_code === 20203) {
        router.replace('/')
      }
      return Promise.reject('error')
    } else {
      return res
    }
  },
  error => {
    console.log('err' + error) // for debug
    if (error.message) {
      if (error.response.data.message) {
        if (error.response.data.message === 'Missing JWT token in request' || error.response.data.message === 'Invalid user key in JWT token') {
          if (store.getters.device_type === "pc") {
            Message({
              message: "token登录时间已过期，请重新登录！",
              type: 'error',
              duration: 5 * 1000
            })
          }
          if (store.getters.device_type === "mobile") {
            Toast("token登录时间已过期，请重新登录！")
          }
          removeToken();
          router.go(0);
          store.dispatch('refresh_token')
        }
      }
    } else {
      if (store.getters.device_type == "pc") {
        Message({
          message: error.message,
          type: 'error',
          duration: 5 * 1000
        })
      }
      if (store.getters.device_type == "mobile") {
        Toast(error.message)
      }
    }
    return Promise.reject(error)
  }
)

export default service



