import request from '@/utils/request'
//短信登录
export function login_from(data) {
    return request({
        url: 'account/quicklogin',
        method: 'post',
        data
    })
}
//密码登录
export function login_password_from(data) {
    return request({
        url: 'account/login',
        method: 'post',
        data
    })
}


