<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
import { getUrlParam } from "@/utils/param";
import { setIp } from "@/utils/auth";
export default {
  name: "App",
  data() {
    return {
      isMobile: null,
    };
  },
  computed: {
    // device_type() {
    //   return this.$store.state.app.device_type;
    // }
  },
  created() {
    this.$nextTick(() => {
      setIp();
      this.getParam();
      // window.location.href=this.url
      // let _this = this;
      // window.onresize = () => {
      //   return (() => {
      //     if (_isMobile()) {
      //       if (_this.device_type != "mobile") {
      //         _this.$store.commit("SET_DEVICE_TYPE", "mobile");
      //         _this._ifIsMobile();
      //         return;
      //       }
      //       return;
      //     } else {
      //       if (_this.device_type != "pc") {
      //         _this.$store.commit("SET_DEVICE_TYPE", "pc");
      //         _this._ifIsMobile();
      //         return;
      //       }
      //       return;
      //     }
      //   })();
      // };
      // this.$store.dispatch("if_Device");
    });
  },
  methods: {
    getParam() {
      let redirect_url = getUrlParam("redirect_url");
      let agentID = getUrlParam("agentID");
      let redirect = null;
      if (redirect_url && agentID) {
        redirect = { redirect_url, agentID };
      }
      this.$store
        .dispatch("saveRedirectObject", redirect)
        .then((data) => {
          console.log(data);
        })
        .catch((error) => {
          if (error === "redirect_object:undefined") {
            console.log(error);
          } else {
            console.log(error);
            this.$router.push({ name: "account" });
          }
        });
    },
  },
};
</script>




