import Cookies from 'js-cookie'
import { getUrl, setUrl, removeUrl, getIp } from "@/utils/auth";
import { _isMobile } from "@/utils/validate.js";
// import router from '@/router/index'

const app = {
    state: {
        device_type: Cookies.get('device_type'),
        redirect_object: getUrl(),
        ip: getIp()
    },
    mutations: {
        SET_DEVICE_TYPE: (state, type) => {
            Cookies.set('device_type', type)
            state.device_type = type
        },
        SET_REDIRECT_OBJECT: (state, object) => {
            state.redirect_object = object
        },
    },
    actions: {
        //判断设备
        if_Device({ commit, state }) {
            if (_isMobile()) {
                if (state.device_type != "mobile") {
                    commit("SET_DEVICE_TYPE", "mobile");
                    // router.replace(`/mobile`)
                }
            } else {
                if (state.device_type != "pc") {
                    commit("SET_DEVICE_TYPE", "pc");
                    // router.replace(`/pc`)
                }
            }
            // this.$router.replace(`/${this.device_type}`);
        },
        saveRedirectObject({ commit, dispatch }, info) {
            const Info = info
            if (Info) {
                commit('SET_REDIRECT_OBJECT', Info)
                setUrl(Info)
            }
            return new Promise((resolve, reject) => {
                dispatch('refresh_token', { root: true }).then(data => resolve(data)).catch(error => {
                    reject(error)
                })
            })
        },
        redirect({ state }, info) {
            const data = info
            // console.log(data)
            // console.log(state)
            if (data.agent_token) {
                const obj = state.redirect_object
                // console.log(obj)
                removeUrl()
                let redirect_uri = decodeURIComponent(obj.redirect_url)
                let token = encodeURIComponent(data.agent_token)
                // console.log(redirect_uri + '?token=' + data.agent_token)
                window.location.href = redirect_uri + '?token=' + token;
            }
        }
    }
}

export default app



