// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store';

//样式
import './assets/styles/style.css';
import './styles/reset.scss'

//全部函数
import config from './lib/function'

import './permission' // permission control
Vue.use(config);

//element-ui
import {
  Input,
  Button,
  Icon,
  Form,
  FormItem,
  Image,
  Checkbox,
  Tabs,
  TabPane,
  Tag,
  Row,
  Col,
  Divider,
  Message,
  Loading,
  MessageBox,
  Select,
  Option,
  Container,
  Header,
  Main,
  Footer,
  Avatar,
  scrollbar
} from 'element-ui';
import '@/icons';

//vant
import { Overlay, Icon as VanIcon, Toast, Image as VanImage, Lazyload, Cell, CellGroup, Dialog, Tab, Tabs as VanTabs, ActionSheet, Button as VanButton, List   } from 'vant';
let base64 = require('js-base64').Base64;

//vant
Vue.use(Lazyload, {
  lazyComponent: true,
});
Vue.use(VanImage);
Vue.use(Toast);
Vue.use(Overlay);
Vue.use(VanIcon);
Vue.use(Cell);
Vue.use(CellGroup);
Vue.use(Dialog);
Vue.use(VanButton);
Vue.use(Tab);
Vue.use(VanTabs);
Vue.use(ActionSheet);
Vue.use(List);
// Vue.use(Notify);


//element-ui
Vue.use(Divider);
Vue.use(Input);
Vue.use(Row);
Vue.use(Col);
Vue.use(Checkbox);
Vue.use(Icon);
Vue.use(Image);
Vue.use(FormItem);
Vue.use(Select);
Vue.use(Option);
Vue.use(Button);
Vue.use(Form);
Vue.use(Tabs);
Vue.use(Tag);
Vue.use(TabPane);
Vue.use(Container);
Vue.use(Header);
Vue.use(Main);
Vue.use(Footer);
Vue.use(Avatar)
Vue.use(scrollbar)
Vue.use(Loading.directive);

Vue.component(Message.name, Message)
Vue.component(MessageBox.name, MessageBox)
// Vue.component(Notification.name, Notification)

Vue.prototype.$base64 = base64;
Vue.prototype.$loading = Loading.service;
Vue.prototype.$msgbox = MessageBox;
Vue.prototype.$alert = MessageBox.alert;
Vue.prototype.$confirm = MessageBox.confirm;
Vue.prototype.$prompt = MessageBox.prompt;
// Vue.prototype.$notify = Notification;
Vue.prototype.$message = Message;
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



