//设置密码
export function setPassword(data) {
    return request({
        url: 'account/setpass',
        method: 'post',
        data
    })
}
//设置手机号
export function setPhone(data) {
    return request({
        url: 'wechat/relation',
        method: 'post',
        data
    })
}
//获取人机验证
export function getSecurity_Level(data) {
    return request({
        url: 'account/captcha/risk',
        method: 'post',
        data
    })
}
//微信授权
export function wechat_auth_api(params, id) {
    return request({
        url: 'wechat/login/' + id,
        method: 'post',
        params
    })
}
//获取用户信息
export function userInfo() {
    return request({
        url: 'account/user/info',
        method: 'get'
    })
}
//agent列表
export function agents(params) {
    return request({
        url: 'agents',
        method: 'get',
        params
    })
}
//登出
export function removeTokens() {
    return request({
        url: 'token',
        method: 'delete'
    })
}
//修改用户名
export function userName(params) {
    return request({
        url: 'account/username',
        method: 'put',
        params
    })
}
//重置密码
export function resertPass(data) {
    return request({
        url: 'account/resertpass',
        method: 'post',
        data
    })
}

export const removeBind = params => {
    request({
        url: `/account/bind`,
        method: "delete",
        params
    })
}


