import request from '@/utils/request'
import axios from 'axios'

export function getCode(phone) {
    return request({
        url: 'account/sms/'+phone,
        method: 'post',
    })
}

//人工验证码
export function captcha(data) {
    return request({
        url: 'account/captcha',
        method: 'post',
        data
    })
}
